#eff-embed-signup {
  background-color: white;
  text-align: center;
  padding: 20px; 
  @media screen and (min-width: $tabwidth) {
    padding: 40px;
  }
  .popup-form {
    margin-top: 20px;
    iframe {
      overflow: visible;
      width: 100%;
      height: 500px;
      border: none;
    }
  }
}
.template-index {
  > .row-1 {
    position: relative;
    .outer-wrapper {
      .slide-wrapper {
        position: relative;
        padding-bottom: 56.25%;
        @media screen and (min-width: $tabwidth) {
          position: static;
          padding-bottom: 0;
        }
      }
      .slide {
        display: none;
        background-position: center;
        background-size: cover;
      }
      .inner-wrapper {
        padding: 15px;
        @media screen and (min-width: $tabwidth) {
          padding: 15% 4% 10% 4%;
          width: 55%;
        }
        .tag {
          font-size: 30px;
          text-transform: uppercase;
          padding: 0 0 $margin-medium 0;
          color: $color__eff-blue;
          @media screen and (min-width: $tabwidth) {
            @include dropshadow;
            color: white;
          }
        }
        .copy {
          font-size: 25px;
          padding: 0 0 30px 0;
          @media screen and (min-width: $tabwidth) {
            @include dropshadow;
            color: white;
          }
        }
        .button {
          display: inline-block;
          text-align: center;
          background-color: $color__eff-blue;
          color: white;
          font-size: 19px;
          font-weight: 600;
          position: relative;
          text-transform: uppercase;
          &:hover {
            background-color: $color__eff-brown;
          }
          a.surrounding {
            border: 1px solid white;
            padding: $margin-medium $margin-large;
          }
        }
      }
    }
  }
  > .row-2 {
    @media screen and (max-width: $tabwidth) {
      background-image: none !important;
    }
    @media screen and (min-width: $tabwidth) {
      background-color: $color__eff-light-grey;
      @include flexbox();
      @include flexbox-justify(center);
      @include flexbox-align-content(center);
      @include flexbox-align-items(center);
      background-size: auto 60%;
      background-position: right bottom;
      background-repeat: no-repeat;
    }
    > .column-1 {
      width: 100%;
      @media screen and (min-width: $tabwidth) {
        width: 35%;
      }
      @media screen and (min-width: 1100px) {
        width: 24.8%;
      }
      img {
        width: 70%;
        display: block;
        margin: 0 auto 15px auto;
        @media screen and (min-width: $tabwidth) {
          width: 100%;
          margin: 0;
        }
      }
    }
    > .column-2 {
      width: 100%;
      position: relative;
      padding: $margin-medium;
      background-size: auto 60%;
      background-position: right bottom;
      background-repeat: no-repeat;
      background-color: $color__eff-light-grey;
      @media screen and (min-width: $tabwidth) {
        background-image: none !important;
        background-color: transparent;
        width: 65%;
      }
      @media screen and (min-width: 1100px) {
        width: 75%;
      }
      > .wrapper {
        padding: 2% 5%;
        .tag {
          font-size: 30px;
          text-transform: uppercase;
          color: $color__eff-blue;
          font-weight: 300;
          padding: 0 0 $margin-medium 0;
        }
        .copy {
          font-size: 20px;
          padding: 0 0 $margin-large 0;
        }
        .button {
          position: relative;
          text-align: center;
          display: inline-block;
          font-size: 22px;
          text-transform: uppercase;
          color: $color__eff-blue;
          a.surrounding {
            &:hover {
              font-size: 24px;
            }
          }
        }
      }
    }
  }
  > .row-3, > .row-4, > .row-5 {
    margin-bottom: 15px;
    position: relative;
    @media screen and (min-width: $tabwidth) {
      @include flexbox();
      @include flexbox-justify(center);
      @include flexbox-align-content(center);
      @include flexbox-align-items(stretch);
    }
    @media screen and (min-width: 1400px) {
      align-items: center;
    }
    > .column-1 {
      order: 2;
      width: 100%;
      position: relative;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      @media screen and (min-width: $tabwidth) {
        width: 50%;
      }
      img {
        width: 100%;
        display: block;
        @media screen and (min-width: $tabwidth) {
          display: none;
        }
        @media screen and (min-width: 1400px) {
          display: block;
        }
      }
    }
    > .column-2 {
      order: 1;
      width: 100%;
      @media screen and (min-width: $tabwidth) {
        width: 50%;
      }
      &:hover {
        .background {
          background-color: $color__eff-brown;
        }
      }
      .background {
        width: 100%;
        position: absolute;
        height: 100%;
        z-index: -1;
        background-color: $color__eff-blue;
        top: 0;
        left: 0;
      }
      a.surrounding {
        > .wrapper {
          width: 80%;
          margin: 0 auto;
          text-align: center;
          color: white;
          padding: $margin-medium 0;
          @media screen and (min-width: $tabwidth) {
            padding: 8% 0;
          }
          .tag {
            font-size: 27px;
            text-transform: uppercase;
            padding: 0 0 $margin-tiny 0;
          }
          .icon {
            width: 30px;
            margin: 0 auto;
          }
          .copy {
            padding: $margin-medium 0 0 0;
            color: white;
            font-size: 22px;
          }
        }
      }
    }
  }
  > .row-3 {
    > .column-1 {
      img {

      }
    }
    > .column-2 {
      .tag {
      }
      .copy {

      }
    }
  }
  > .row-4 {
    > .column-1 {
      @media screen and (min-width: $tabwidth) {
        order: 1;
      }
      img {

      }
    }
    > .column-2 {
      @media screen and (min-width: $tabwidth) {
        order: 2;
      }
      .tag {
      }
      .copy {

      }
    }
  }
  > .row-5 {
    > .column-1 {
      img {

      }
    }
    > .column-2 {
      .tag {
      }
      .copy {

      }
    }
  }
  >.row-6 {
    margin: $margin-extra-large 0;
    position: relative;
    @media screen and (min-width: $tabwidth) {
      @include flexbox();
      @include flexbox-justify(center);
      @include flexbox-align-content(center);
      @include flexbox-align-items(center);
    }
    @media screen and (min-width: 1400px) {
      align-items: center;
    }
    >.col-1 {
      width: 100%;
      position: relative;
      @media screen and (min-width: $tabwidth) {
        width: 50%;
      }
      img {
        width: 100%;
        display: block;
        @media screen and (min-width: $tabwidth) {
          display: block;
          padding: 0 0 0 20%;
        }
      }
    }
    >.col-2 {
      width: 100%;
      @media screen and (min-width: $tabwidth) {
        width: 50%;
      }
      > .wrapper {
        width: 80%;
        margin: 0 auto;
        padding: $margin-medium 0;
        @media screen and (min-width: $tabwidth) {
          padding: 8% 0;
        }
        header {
          h3 {
            margin: 0 0 30px 0;
            color: $color__eff-blue;
            font-weight: 500;
            font-size: 27px;
          }
          h2 {
            margin: 0 0 10px 0;
            color: $color__eff-blue;
            font-weight: 500;
            font-size: 27px;
          }
        }
        .copy {
          font-size: 22px;
          a {
            font-weight: 500;
            color: $color__eff-brown;
            text-transform: uppercase;
            span {
              margin: 0 0 0 8px;
              vertical-align: top;
              line-height: 1.1;
              display: inline-block;
            }
          }
        }
      }
    }
  }
}

.template-how {
  .row-1.template-header {
    @media screen and (max-width: $tabwidth) {
      margin-bottom: 0;
    }
  }
  > .row-2, > .row-3, > .row-4 {
