      @include flexbox-justify(center);
      @include flexbox-align-content(center);
      @include flexbox-align-items(stretch);
    }
    &:last-child {
      margin-bottom: $margin-extra-large;
    }
    > .column-1 {
      order: 1;
      width: 100%;
      @media screen and (min-width: $tabwidth) {
        width: 51%;
