          img {
            float: left;
            width: 20%;
            margin: 0 4% 3% 0;
          }
          header {
            padding: $margin-medium 0;
            h2 {
              font-weight: 500;
              text-transform: uppercase;
              color: $color__eff-blue;
              margin: 0;
              padding: 0;
            }
          }
          .tag {
            margin: 0 0 3% 0;
            font-style: italic;
            font-family: "adobe-caslon-pro", serif;
            color: $color__eff-blue;
            font-size: 23px;
          }
        }
        .row-2 {

        }
      }
    }
    > .column-2 {
      order: 2;
      width: 100%;
      position: relative;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      @media screen and (min-width: $tabwidth) {
        width: 49%;
      }
      img {
        width: 100%;
        display: block;
        @media screen and (min-width: $tabwidth) {
          display: none;
        }
      }
    }
  }
  > .row-2 {
    > .column-1 {
    }
    > .column-2 {
    }
  }
  > .row-3 {
    > .column-1 {
      @media screen and (min-width: $tabwidth) {
        order: 2;
      }
      > a.surrounding {
      }
      > a >.wrapper,> .wrapper {
        @media screen and (min-width: $tabwidth) {
          padding: 0 0 0 100%-(100%*49/51);
        }
      }
    }
    > .column-2 {
      @media screen and (min-width: $tabwidth) {
        order: 1;
      }
    }
  }
  > .row-4 {
    > .column-1 {
    }
    > .column-2 {
    }
  }
}

