.template-about, .template-page {
  .row-1 {
    /* see template header */
  }
  .row-2 {
    margin: $margin-large 0 0 0;
    @include flexbox();
    @include flexbox-justify(center);
    @include flexbox-align-content(center);
    @include flexbox-align-items(stretch);
    @include flexbox-wrap(wrap);
    padding: 0 0 $margin-large 0;
    .column-1 {
      width: 100%;
      order: 2;
      @media screen and (min-width: $mobilelandscapewidth) {
        width: 50%;
      }
      @media screen and (min-width: $midwidth) {
        width: 32%;
        order: 1;
      }
      > .wrapper {
        padding: 0 0 $margin-medium 0;
        @media screen and (min-width: $mobilelandscapewidth) {
          padding: 0 4% 0 0;
        }
        @media screen and (min-width: $midwidth) {
          padding: 0;
        }
        img {
          display: block;
          margin: 0 auto;
          width: 70%;
          @media screen and (min-width: $mobilelandscapewidth) {
            width: 100%;
          }
        }
      }
    }
    .column-2 {
      width: 100%;
      order: 1;
      @media screen and (min-width: $midwidth) {
        width: 46%;
        order: 2;
      }
      > .wrapper {
        padding: 0 4% $margin-medium 4%;
        @media screen and (min-width: $midwidth) {
          padding: 0 4%;
        }
      }
    }
    aside.column-3 {
      order: 3;
      width: 100%;
      @media screen and (min-width: $mobilelandscapewidth) {
        width: 50%;
      }
      @media screen and (min-width: $midwidth) {
        width: 22%;
      }
      > .outer-wrapper {
        padding: 0 4%;
        height: 100%;
        @media screen and (min-width: $mobilelandscapewidth) {
          padding: 0 0 0 4%;
        }
        @media screen and (min-width: $midwidth) {
          padding: 0;
        }
        > .inner-wrapper {
          height: 100%;
          background-color: $color__eff-blue;
          width: 100%;
          margin: 0;
          background-size: 80% auto;
          background-position: top center;
          background-repeat: no-repeat;
          @media screen and (min-width: $midwidth) {
            background-size: 100% auto;
          }
          blockquote {
            font-family: "adobe-caslon-pro", serif;
            padding: 50px 0 $margin-large 0;
            p {
              color: white;
              font-style: italic;
              font-size: 23px;
            }
          }
        }
      }
    }
  }
}
.template-one-column {
  .row-2 {
    >.wrapper {
      padding: 0 0 $margin-medium 0;
      text-align: center;
      @media screen and (min-width: $tabwidth) {
        padding: 0 4% $margin-medium 4%;
      }
      a.button, button[type=submit] {
        background-color: $color__eff-blue;
        border: 1px solid $color__eff-blue;
        color:white;
        &:hover {
          background-color: $color__eff-brown;
          border: 1px solid $color__eff-brown;
        }
      }
    }
  }
}
.template-contact, .template-single, .template-archive, .template-blog {
  .row-1 {
    /* see template header */
  }
  .row-2 {
    margin: $margin-large 0 0 0;
    @include flexbox();
    @include flexbox-justify(center);
    @include flexbox-align-content(center);
    @include flexbox-align-items(stretch);
    @include flexbox-wrap(wrap);
    padding: 0 0 $margin-large 0;
    .column-1 {
      width: 100%;
      order: 1;
      @media screen and (min-width: $tabwidth) {
        width: 78%;
      }
      .gform_wrapper {
        @media screen and (min-width: $tabwidth) {
          width: 65%;
        }
      }
      > .wrapper {
        padding: 0 4% $margin-medium 4%;
        @media screen and (min-width: $tabwidth) {
          padding: 0 4% 0 0;
        }
      }
    }
    aside.column-2 {
      order: 2;
      width: 100%;
      @media screen and (min-width: $tabwidth) {
        width: 22%;
      }
      > .outer-wrapper {
        padding: 0 4%;
        height: 100%;
        @media screen and (min-width: $tabwidth) {
          padding: 0;
        }
        > .inner-wrapper {
          height: 100%;
          background-color: $color__eff-blue;
          width: 100%;
          margin: 0;
          background-size: 80% auto;
          background-position: top center;
          background-repeat: no-repeat;
          @media screen and (min-width: $tabwidth) {
            background-size: 100% auto;
          }
          #mc_embed_signup {
            padding: 50px 20px $margin-large 20px;
            form {
              #mc_embed_signup_scroll {
                p{
                  color: white;
                  font-size: 15px;
                  text-transform: uppercase;
                  font-weight: 500;
                  margin: $margin-medium 0 $margin-small 0;
                  @media screen and (min-width: $tabwidth) {
                    margin: 0 0 $margin-small 0;
                  }
                }
                .mc-field-group {
                  display: block;
                  margin-bottom: 8px;
                  input {
                    border: none;
                    border-radius: 0px;
                    color: $color__eff-brown;
                    line-height: 1.6;
                    width: 100%;
                    font-size: 15px;
                    &::-moz-placeholder, &::-webkit-input-placeholder,
                    &:-moz-placeholder, &:-ms-input-placeholder {
                      color: $color__eff-brown;
                      font-size: 15px;
                    }
                  }
                  .mce_inline_error {
                    margin-top: 8px;
                    text-transform: uppercase;
                  }
                }
                #mce-responses {
                  margin-bottom: $margin-tiny;
                  color: white;
                  #mce-error-response {

                  }
                  #mce-success-response {

                  }
                }
                input[type="submit"]{
                  display: inline-block;
                  margin-top: 8px;
                  border: none;
                  background: none;
                  box-shadow: none;
                  border-radius: 0px;
                  padding: $margin-small $margin-medium;
                  background-color: white;
                  color: $color__eff-brown;
                  font-size: 18px;
                  text-transform: uppercase;
                  text-align: center;
                  min-width: 130px;
                  line-height: 1;
                  &:hover {
                    background-color: $color__eff-brown;
                    color: white;
                  }
                }
              }
            }
          }
          blockquote p {
            font-style: italic;
          }
          blockquote, .list {
            font-family: "adobe-caslon-pro", serif;
            padding: 50px 0 $margin-large 0;
            header h2, p {
              color: white;
              font-size: 23px;
              padding: 0 20px;
            }
            ul {
              margin: 0;
              padding: 0;
              li {
                list-style-type: none;
                a {
                  color: white;
                  &:hover {
                    color: $color__eff-brown;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
