.bannerSlides {
  width: 100%;
  height: 430px;
  overflow: hidden;
  position: relative;
  margin: 0 auto;
  @media screen and (min-width: $tabwidth) {
    height: 450px;
  }
  @media screen and (min-width: $midwidth) {
    height: 550px;
  }
  @media screen and (min-width: $deskwidth) {
    height: 600px;
  }
  .flex-viewport,
  .slideshow-wrapper {
    width: 100%;
    height: 100%;
    margin: 0 0;
    padding: 0 0;
    list-style: none;
  }
  .slide-item {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: #EEE;
    height: 100%;
    width: 100%;
  }

  .caption {
    display: block;
    width: 100%;
    height: 100%;
    outline: none;
    text-decoration: none;
    position: relative;
    color: $color-text-grey;
    text-align: center;
  }
  .caption.has-link {
    cursor: pointer;
  }
  .inner-text {
    display: block;
    width: 100%;
    position: absolute;
    bottom: 35px;
    opacity: 0;
    @include transition(all ease 1.5s);
    @media screen and (min-width: $tabwidth) {
      bottom: 30px;
    }
  }

  .caption.fadeIn .inner-text {
    opacity: 1;
  }

  .textwrap {
    display: block;
    box-sizing: border-box;
    background: rgba(247,234,221,.75);
    padding: 1.875rem;
    width: 90%;
    margin: 0 auto;
    @media screen and (min-width: $tabwidth) {
      width: 265px;
      min-height: 300px;
      float: right;
      margin-right: 30px;
      @include flexbox();
      @include flexbox-align-items(center);
    }
  }

  .icon, .text,
  .b-title, .b-text {
    display: block;
    width: 100%;
  }

  .b-title {
    margin: 0 0;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 1.2;
  }

  .b-text {
    margin-top: 25px;
    line-height: 1.3;
  }

  .icon i {
    display: inline-block;
    width: 25px;
    height: 25px;
    background: url('images/floral.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }

  .flex-control-nav {
    position: absolute;
    bottom: 0;
    z-index: 50;
    width: 100%;
    box-sizing: border-box;
    height: 23px;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
