.instagram-image-div {
  .instalink, img {
    display: block;
    width: 100%;
    float: left;
    position: relative;
  }
  img {
    height: auto;
  }
  .instalink {
    text-decoration: none;
    color: #FFF;
  }
  .caption {
    box-sizing: border-box;
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(126,161,157,0.95);
    color: #FFF;
    z-index: 5;
    opacity: 0;
    visibility: hidden;
    text-align: center;
    overflow: hidden;
    @include transition(all ease .3s);
  }
  .txtwrap {
    display: block;
    position: relative;
    padding: 10px 30px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 16px;
    font-weight: 300;
    width: 100%;
    box-sizing: border-box;
  }
  .icon {
    display: inline-block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 11px;
    font-size: 13px;
    z-index: 7;
    margin-right: 0;
    @include transition(all ease .3s);
  }
  .instalink:hover  {
    .icon {
      margin-right: -6px;
    }
    .caption {
      opacity: 1;
      visibility: visible;
    }
  }
}
.site-footer .email-signup {
  .mc-field-group input {
    box-sizing: border-box;
    padding: 3px 8px;
    width: 100%;
    outline: none;
