    @include transition(all ease-out .3s);
  }
  .mc-field-group input:focus {
    box-shadow: 0 0 0 3px rgba(163, 239, 250, 0.65);
  }
}
.the-top-navigation {
  position: relative;
}
.burgerMenu {
  display: block;
  width: 26px;
  height: 25px;
  position: absolute;
  top: 10px;
  cursor: pointer;
  span {
    position: relative;
    margin-top: 9px;
    margin-bottom: 9px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    position: absolute;
  }
  span,
  span:before,
  span:after {
    display: block;
    width: 100%;
    height: 2px;
    background-color: #8c7e77;
  }
  span:before,
  span:after {
    content: "";
    position: absolute;
  }
  span:before {
    top: -9px;
  }
  span:after {
    top: 9px;
  }
}
